import { useState } from "react";
import { Button, Col, Form, FormLabel, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import {
  createDoctorSchedule,
  getAllSlots,
} from "../../../../app/features/doctorSchedule/doctorSchedule.slice";
import { useDispatch, useSelector } from "react-redux";
import {
  convertTimeIntoUTC,
  getCurrentDate,
  getCurrentTime,
} from "../../../../components/GenericActions";
import { FieldError } from "../../../../components/ErrorMessage/ErrorMessage";
import moment from "moment";
import GenericDatePicker from "../../../../components/GenericDatePicker";

function SingleDaySlot(props) {
  const { handleClose } = props;

  const { handleSubmit, register } = useForm();
  const [endTime, setEndTime] = useState("");
  const [startDate, setStartDate] = useState(null); //1
  const [startTime, setStartTime] = useState("");
  const [validationMessage, setValidationMessage] = useState("");

  const dispatch = useDispatch();
  const { allSlots } = useSelector((state) => state.doctorSchedule);

  const filteredSlots = allSlots?.filter((slot) => !slot.isBooked);
  const startTimes = filteredSlots?.map((slot) => slot.startTime);
  const endTimes = filteredSlots?.map((slot) => slot.endTime);

  const extractedData = allSlots?.map((slot) => ({
    scheduleId: slot.scheduleId,
    Fee: slot.Fee,
  }));

  const handleStartDateChange = (dateValue) => {
    debugger
    const selectedDate = dateValue;
    setStartDate(selectedDate);
    setStartTime(getCurrentTime());
    setEndTime(endTimes[1]);
  };
  
  function getDayName(date = startDate, locale = "en-US") {
    debugger;
    return date?.toLocaleDateString(locale, { weekday: "long" });
  }
  const dayNamesArray = [];
  dayNamesArray.push(getDayName());

  const moveToNext = () => {
    const standardISODate = moment(startDate).format("YYYY-MM-DD");
    handleClose();
    const finalData = {
      date: standardISODate,
      month: null,
    };
    dispatch(getAllSlots(finalData));
  };

  function onSubmit(data) {
    const standardISODate = moment(startDate).format("YYYY-MM-DD");

    const finalData = {
      startDate: standardISODate,
      endDate: standardISODate,
      startTime: convertTimeIntoUTC(startTime, standardISODate)?.split("Z")[0],
      endTime: convertTimeIntoUTC(endTime, standardISODate)?.split("Z")[0],
      noOfDoctors: 1,
      daysOfWeek: dayNamesArray,
      fee: data?.feeValue,
    };
    dispatch(createDoctorSchedule({ finalData, moveToNext }));
  }

  //   const dateForPicker = (dateString) => {
  //     return moment(new Date(dateString)).format('YYYY-MM-DD')
  // };

  const handleEndTimeChange = (e) => {
    const selectedEndTime = e.target.value;
    setEndTime(selectedEndTime);

    // Check if the time difference is less than 15 minutes
    const isValidTimeDifference = validateTimeDifference(
      startTime,
      selectedEndTime
    );

    if (!isValidTimeDifference) {
      setValidationMessage(
        "The appointment start time and end time should be at least 15 minutes apart."
      );
    } else {
      setValidationMessage("");
    }
  };

  const validateTimeDifference = (start, end) => {
    const standardISODate = moment(startDate).format("YYYY-MM-DD");

    const startTime = new Date(`${standardISODate} ${start}`);
    const endTime = new Date(`${standardISODate} ${end}`);
    let timeDifference = (endTime - startTime) / (1000 * 60); // in minutes

    // Adjust for negative time difference (crossing over to the next day)
    if (timeDifference < 0) {
      timeDifference += 24 * 60; // add 24 hours in minutes
    }

    return timeDifference >= 15;
  };

  return (
    <Form onSubmit={handleSubmit(onSubmit)}>
      <Row>
        <Col xl={12}>
          <label className="fw-semibold fs-6">Date</label>
          <GenericDatePicker
            selectedDateState={startDate}
            onChange={handleStartDateChange}
            useDrDateClass={false}
            dateFormat="dd/MM/yyyy"
            placeholderText="day/month/year"
            mindate={new Date()}
          />
          {/* <Form.Group controlId="formDate" className="mb-3">
          

            <FormLabel className="fw-semibold fs-6">Date</FormLabel>
            <Form.Control
              type="date"
              name="date"
              placeholder={startDate ? dateForPicker(startDate) : "dd/mm/yyyy"}
              className="custom-date"
              value={startDate}
              min={getCurrentDate()}
              onChange={(date) => handleStartDateChange(date)}
            />
          </Form.Group> */}
        </Col>
        <Col md={6} xl={6}>
          <Form.Group controlId="start-timeId" className="mb-3">
            <FormLabel className="fw-semibold fs-6">Start Time</FormLabel>
            <Form.Control
              type="time"
              placeholder="HH:MM"
              name="startTime"
              className="custom-date"
              min={startDate || getCurrentTime()}
              disabled={!startDate}
              value={startTime ? startTime : startTimes?.[0]}
              onChange={(e) => setStartTime(e.target.value)}
            />
          </Form.Group>
        </Col>
        <Col md={6} xl={6}>
          <Form.Group controlId="end-timeId" className="mb-3">
            <FormLabel className="fw-semibold fs-6">End Time</FormLabel>
            <Form.Control
              type="time"
              placeholder="HH:MM"
              className="custom-date"
              name="endTime"
              min={startTime}
              value={endTime ? endTime : endTimes?.[1]}
              disabled={!startTime}
              // onChange={(e) => setEndTime(e.target.value)}
              onChange={handleEndTimeChange}
            />
          </Form.Group>
        </Col>
        {validationMessage && <FieldError message={validationMessage} />}

        <Col md={6} xl={6}>
          <Form.Group controlId="fee">
            <Form.Label className="fw-bold">Fee (€)</Form.Label>
            <Form.Control
              size="lg"
              type="number"
              step="0.01"
              placeholder="Enter Fee (1.36)"
              min={0}
              {...register("feeValue", { required: true })}
              defaultValue={
                extractedData?.length > 0 ? extractedData[0].Fee : ""
              }
              required
            />
          </Form.Group>
        </Col>
        <div className="d-grid mt-4">
          <Button
            className="block"
            variant="primary primary_bg"
            size="lg"
            type="submit"
            disabled={validationMessage}
          >
            Create Slot
          </Button>
        </div>
      </Row>
    </Form>
  );
}

export default SingleDaySlot;
