import { useState } from "react";
import { Button, Col, Form, FormLabel, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import moment from "moment";
import { convertTimeIntoUTC } from "../../../../components/GenericActions";
import {
  createDoctorSchedule,
  getAllSlots,
} from "../../../../app/features/doctorSchedule/doctorSchedule.slice";
import { useDispatch, useSelector } from "react-redux";
import { FieldError } from "../../../../components/ErrorMessage/ErrorMessage";
import GenericDatePicker from "../../../../components/GenericDatePicker";

function RecurringSlot(props) {
  const { handleClose } = props;
  const { handleSubmit, register } = useForm();

  const [startDate1, setStartDate1] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [startTimeRecurring, setStartTimeRecurring] = useState("");
  const [endTimeRecurring, setEndTimeRecurring] = useState("");
  const [weekDays, setWeekDays] = useState([]);
  const [validationMessage, setValidationMessage] = useState("");

  const dispatch = useDispatch();
  const handleStartDateChange1 = (dateValue) => {
    const selectedDate = dateValue;
    setStartDate1(selectedDate);
    // setStartTimeRecurring(getCurrentTime());

    // Calculate the minimum end date
    const minEndDate = new Date(selectedDate);
    // minEndDate.setDate(minEndDate.getDate()); // Add one day to start date
    // const minEndDateStr = minEndDate.toISOString().split("T")[0]; // Format as YYYY-MM-DD

    const minEndDateStr = moment(minEndDate).format("YYYY-MM-DD"); // Format as YYYY-MM-DD
    const endDateStr = moment(endDate).format("YYYY-MM-DD"); // Format as YYYY-MM-DD

    // Update the end date if it's before the minimum end date
    if (endDateStr < minEndDateStr) {
      setEndDate(new Date(minEndDateStr));
    }
  };

  const { allSlots } = useSelector((state) => state.doctorSchedule);
  const filteredSlots = allSlots?.filter((slot) => !slot.isBooked);
  const startTimes = filteredSlots?.map((slot) => slot.startTime);
  const endTimes = filteredSlots?.map((slot) => slot.endTime);

  const extractedData = allSlots?.map((slot) => ({
    scheduleId: slot.scheduleId,
    Fee: slot.Fee,
  }));
  const handleCheckboxChange = (e) => {
    const { value, checked } = e.target;

    if (checked) {
      // Add the value to the array
      setWeekDays((prevWeekDays) => [...prevWeekDays, value]);
    } else {
      // Remove the value from the array
      setWeekDays((prevWeekDays) =>
        prevWeekDays.filter((day) => day !== value)
      );
    }
  };
  const moveToNext = () => {
    const standardISODate = moment(startDate1).format("YYYY-MM-DD");
    handleClose();
    const finalData = {
      date: standardISODate,
      month: null,
    };
    dispatch(getAllSlots(finalData));
  };
  function onSubmit(data) {
    const standardISODate = moment(startDate1).format("YYYY-MM-DD");
    const standardISOEndDate = moment(endDate).format("YYYY-MM-DD");

    const finalData = {
      startDate: standardISODate,
      endDate: standardISOEndDate,
      startTime: convertTimeIntoUTC(startTimeRecurring, standardISODate)?.split(
        "Z"
      )[0],
      endTime: convertTimeIntoUTC(endTimeRecurring, standardISODate)?.split(
        "Z"
      )[0],
      noOfDoctors: 1,
      daysOfWeek: weekDays,
      fee: data?.feeValue,
    };
    dispatch(createDoctorSchedule({ finalData, moveToNext }));
  }

  const handleEndTimeChange = (e) => {
    const selectedEndTime = e.target.value;
    setEndTimeRecurring(selectedEndTime);

    // Check if the time difference is less than 15 minutes
    const isValidTimeDifference = validateTimeDifference(
      startTimeRecurring,
      selectedEndTime
    );

    if (!isValidTimeDifference) {
      setValidationMessage(
        "The appointment start time and end time should be at least 15 minutes apart."
      );
    } else {
      setValidationMessage("");
    }
  };

  const validateTimeDifference = (start, end) => {
    const standardISODate = moment(startDate1).format("YYYY-MM-DD");
    const startTime = new Date(`${standardISODate} ${start}`);
    const endTime = new Date(`${standardISODate} ${end}`);
    let timeDifference = (endTime - startTime) / (1000 * 60); // in minutes

    // Adjust for negative time difference (crossing over to the next day)
    if (timeDifference < 0) {
      timeDifference += 24 * 60; // add 24 hours in minutes
    }

    return timeDifference >= 15;
  };

  return (
    <>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <div key="0" className="mb-3 border p-3 rounded modalCheckboxes">
          <Form.Check
            type="checkbox"
            id=""
            label="Monday"
            className="d-flex"
            value="Monday"
            onChange={handleCheckboxChange}
          />
          <Form.Check
            type="checkbox"
            id=""
            label="Tuesday"
            className="d-flex"
            value="Tuesday"
            onChange={handleCheckboxChange}
          />
          <Form.Check
            type="checkbox"
            id=""
            label="Wednesday"
            className="d-flex"
            value="Wednesday"
            onChange={handleCheckboxChange}
          />
          <Form.Check
            type="checkbox"
            id=""
            label="Thursday"
            className="d-flex"
            value="Thursday"
            onChange={handleCheckboxChange}
          />
          <Form.Check
            type="checkbox"
            id=""
            label="Friday"
            className="d-flex"
            value="Friday"
            onChange={handleCheckboxChange}
          />
          <Form.Check
            type="checkbox"
            id=""
            label="Saturday"
            className="d-flex"
            value="Saturday"
            onChange={handleCheckboxChange}
          />
          <Form.Check
            type="checkbox"
            id=""
            label="Sunday"
            className="d-flex"
            value="Sunday"
            onChange={handleCheckboxChange}
          />
        </div>

        <Row>
          <Col md={6} xl={6}>
            <div className="mb-3">
              <label className="fw-semibold fs-6">Start Date</label>
              <GenericDatePicker
                selectedDateState={startDate1}
                onChange={handleStartDateChange1}
                useDrDateClass={false}
                dateFormat="dd/MM/yyyy"
                placeholderText="day/month/year"
                mindate={new Date()}
              />
            </div>
            <Form.Group controlId="start-timeId" className="mb-3">
              <FormLabel className="fw-semibold fs-6">Start Time</FormLabel>
              <Form.Control
                type="time"
                placeholder="HH:MM"
                className="custom-date"
                // min={getCurrentTime()}
                disabled={!moment(startDate1).format("YYYY-MM-DD")}
                // disabled={!startDate1 || startTimes[0]}
                value={
                  startTimeRecurring ? startTimeRecurring : startTimes?.[0]
                }
                onChange={(e) => setStartTimeRecurring(e.target.value)}
              />
            </Form.Group>
          </Col>
          <Col md={6} xl={6}>
            <div className="mb-3">
              <label className="fw-semibold fs-6">End Date</label>
              <GenericDatePicker
                selectedDateState={endDate}
                onChange={(dateValue) => setEndDate(dateValue)}
                useDrDateClass={false}
                dateFormat="dd/MM/yyyy"
                placeholderText="day/month/year"
                mindate={new Date(startDate1)}
                disabled={!startDate1}
              />
            </div>

            <Form.Group controlId="end-timeId" className="mb-3">
              <FormLabel className="fw-semibold fs-6">End Time</FormLabel>
              <Form.Control
                type="time"
                placeholder="HH:MM"
                className="custom-date"
                min={startTimeRecurring}
                value={endTimeRecurring ? endTimeRecurring : endTimes?.[1]}
                disabled={!startTimeRecurring}
                // disabled={!startTimeRecurring || endTimes[1]}
                onChange={handleEndTimeChange}
              />
            </Form.Group>
          </Col>
          {validationMessage && <FieldError message={validationMessage} />}
          <Col md={6} xl={6}>
            <Form.Group controlId="fee">
              <Form.Label className="fw-bold">Fee (€)</Form.Label>
              <Form.Control
                size="lg"
                type="number"
                step="0.01"
                placeholder="Enter Fee (1.36)"
                min={0}
                {...register("feeValue", { required: true })}
                defaultValue={
                  extractedData?.length > 0 ? extractedData[0].Fee : ""
                }
              />
            </Form.Group>
          </Col>

          <div className="d-grid mt-4">
            <Button
              className="block primary_bg"
              variant="primary"
              size="lg"
              type="submit"
              disabled={validationMessage}
            >
              Create Slot
            </Button>
          </div>
        </Row>
      </Form>
    </>
  );
}

export default RecurringSlot;
