import React, { useState, useEffect, useMemo } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";

import AddNewSlot from "./AddNewSlot";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import {
  getAllSlots,
  updateDoctorSchedule,
} from "../../../app/features/doctorSchedule/doctorSchedule.slice";
import FormModal from "../../../components/Modal/FormModal";
import { useForm } from "react-hook-form";
import { FaCircle } from "react-icons/fa";
import GenericDatePicker from "../../../components/GenericDatePicker";

const Slots = () => {
  const dispatch = useDispatch();
  const { handleSubmit, register } = useForm();
  const [slotDate, setSlotDate] = useState(new Date());
  const [show, setShow] = useState(false);
  const [showUpdate, setShowUpdate] = useState(false);
  const handleShow = () => setShow(true);
  const handleShowUpdate = () => setShowUpdate(true);
  const handleCloseUpdate = () => setShowUpdate(false);
  const handleClose = () => setShow(false);
  const { allSlots } = useSelector((state) => state.doctorSchedule);

  function getDayName(date = new Date(slotDate), locale = "en-US") {
    return date?.toLocaleDateString(locale, { weekday: "long" });
  }
  const dayName = getDayName();
  const displayDate = useMemo(() => {
    return slotDate
      ? `${dayName} - ${moment(slotDate).format("DD-MM-YYYY")}`
      : "Select Date";
  }, [slotDate, dayName]);

  useEffect(() => {
    if (slotDate) {
      const finalData = {
        date: moment(slotDate).format("YYYY-MM-DD"),
        month: null,
      };
      dispatch(getAllSlots(finalData));
    }
  }, [dispatch, slotDate]);

  let fData = allSlots?.filter((slot) => slot.isBooked);
  let notBookData = allSlots?.filter((slot) => !slot.isBooked);

  function onSubmit(data) {
    const updateFeeData = {
      fee: data?.feeValue,
    };
    dispatch(updateDoctorSchedule({ updateFeeData }));
    handleCloseUpdate();
  }
  return (
    <>
      <div className="d-flex justify-content-between align-items-center mb-3 mobileFlex_Col">
        <h5>Created Slots</h5>
        {moment(slotDate).format("YYYY-MM-DD") >= moment(new Date()).format("YYYY-MM-DD") && (
          <div>
            {((allSlots?.length > 0 && fData?.length <= 0) ||
              notBookData?.length > 0) && (
              <Button
                onClick={() => {
                  handleShowUpdate();
                }}
                className="btn-primary primary_bg me-2"
              >
                Update Fees
              </Button>
            )}
            <Button
              onClick={() => {
                handleShow();
              }}
              className="btn-primary primary_bg"
            >
              Create Slot
            </Button>
          </div>
        )}
      </div>

      <Card className="slotsSection">
        <Card.Body>
          <Row className="d-flex justify-content-between align-items-center mb-4">
            <Col xl={2} lg={4} md={4} sm={6}>
              <GenericDatePicker
                selectedDateState={slotDate}
                onChange={(date) => setSlotDate(date)}
                useDrDateClass={false}
                dateFormat="dd/MM/yyyy"
                placeholderText="day/month/year"
              />
            </Col>
            <Col md={4} lg={4} className="text-center">
              <h5 className="text-black fw-bold mb-0">{displayDate}</h5>
            </Col>
            <Col md={4} sm={4}>
              <div className="d-flex justify-content-end radioGroup">
                <div className="d-flex align-items-center me-sm-4 me-2">
                  <span className="bookedSlot rounded-circle me-2">
                    <FaCircle color="#ff6060" size="22" />
                  </span>
                  <span>Booked Slots</span>
                </div>
                <div className="d-flex align-items-center">
                  <span className="availableSlot rounded-circle me-2">
                    <FaCircle color="#81d363" size="22" />
                  </span>
                  <span>Available Slots</span>
                </div>
              </div>
            </Col>
          </Row>

          <div>
            <div className="slotContainer disable__btn-hover">
              {allSlots !== null && allSlots?.length > 0 ? (
                allSlots?.map((slotItem) => {
                  return (
                    <Button
                      key={slotItem?.startTime}
                      className={`slot_btn${
                        slotItem?.isBooked ? " booked" : ""
                      }`}
                    >
                      {slotItem?.startTime} - {slotItem?.endTime}
                    </Button>
                  );
                })
              ) : (
                <p className="text-center">No Slot Available</p>
              )}
            </div>
          </div>
        </Card.Body>
        <AddNewSlot show={show} handleClose={handleClose} />
      </Card>
      <FormModal
        className="modal-lg"
        title="Update Slot Fees"
        handleClose={handleCloseUpdate}
        show={showUpdate}
      >
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Row>
            <Col sx={12}>
              <Form.Group controlId="fee">
                <Form.Label className="fw-bold">Fee (€)</Form.Label>
                <Form.Control
                  size="lg"
                  type="number"
                  step="0.01"
                  placeholder="Enter Fee (1.36)"
                  min={0}
                  {...register("feeValue", { required: true })}
                  required
                />
              </Form.Group>
            </Col>
            <div className="d-grid mt-4">
              <Button
                className="block"
                variant="primary primary_bg"
                size="lg"
                type="submit"
              >
                Update Fee
              </Button>
            </div>
          </Row>
        </Form>
      </FormModal>
    </>
  );
};

export default Slots;
