import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import FormModal from "../../../components/Modal/FormModal";
import RecurringSlot from "./Component/RecurringSlot";
import SingleDaySlot from "./Component/SingleDaySlot";

export default function AddNewSlot({ children, ...rest }) {
  const { show, handleClose } = rest;

  return (
    <div id="AddSlot_modal" className="AddSlot_modal">
      <FormModal
        className="modal-lg"
        title="Time Slot Creation"
        handleClose={handleClose}
        show={show}
      >
        <div className="px-2 pb-3 addSlot_content">
          <Tabs
            defaultActiveKey="1"
            id="uncontrolled-tab-example slot_tabs"
            className="mb-1 slot_tabs"
          >
            {/* Tab 1 */}
            <Tab eventKey="1" className="slot_tab" title="Single Day">
              <SingleDaySlot
                handleClose={handleClose}
              />
            </Tab>

            {/* Tab 2 */}
            <Tab eventKey="2" title="Recurring" className="slot_tab">
              <RecurringSlot
                handleClose={handleClose}
              />
            </Tab>
          </Tabs>
        </div>
      </FormModal>
    </div>
  );
}
