import { Modal } from "react-bootstrap";

export default function FormModal({ children, ...rest }) {
  const { handleClose, show, title, className } = rest;
  return (
    <div>
      <Modal show={show} onHide={handleClose} className={className} backdrop="static">
        <Modal.Header className="py-3" closeButton>
          <Modal.Title className="modalTitle">
            <span className="font-weight-600">{title}</span>
          </Modal.Title>
        </Modal.Header>

        <Modal.Body>{children}</Modal.Body>
      </Modal>
    </div>
  );
}
