import React from "react";
import ReactDatePicker from "react-datepicker";
import { BsCalendarFill } from "react-icons/bs";
import "react-datepicker/dist/react-datepicker.css";

const GenericDatePicker = ({
  selectedDateState,
  onChange,
  dateFormat = "dd MMM yyyy",
  placeholderText = "Date",
  className = "",
  datePickerClassName = "custom-field-picker px-2",
  useDrDateClass = true,
  ...props
}) => {
  console.log("disabled", props);
  return (
    <div className={`position-relative d-flex mb-3 ${className}`} {...props}>
      <ReactDatePicker
        selected={selectedDateState}
        onChange={onChange}
        dateFormat={dateFormat}
        placeholderText={placeholderText}
        className={`${datePickerClassName} ${
          useDrDateClass ? "dr-date-w" : "w-100"
        }`}
        disabled={props?.disabled}
        minDate={props?.mindate}
      />
      <BsCalendarFill size={18} className="custom__date_icon" />
    </div>
  );
};

export default GenericDatePicker;
